import React from "react"
import { Link, graphql } from "gatsby"
import { css } from "@emotion/core"
import { rhythm } from "../utils/typography"
import Layout from "../components/layout"
import { Helmet } from "react-helmet"
import { Flex, Box} from "@rebass/grid/"

export default ({ data }) => {
    console.log(data)
    return (
        <Layout>
            <Flex p={5} flexWrap='wrap' mt={1} width={1}>
                <Helmet>
                    <meta charSet="utf-8" />
                    <title>{data.site.siteMetadata.title}</title>
                    <link rel="" target="_blank" href="http://twitter.com/positivneutron" />
                </Helmet>
                <Box width={[ 1, 1/2 ]} css={css`
                    text-align: center;
                `}>
                    <img
                        src={data.site.siteMetadata.profilPicture}
                        alt="positive-neutron"
                        css={css`
                            border-radius:50%;
                            height: auto;
                            width: 50%;
                        `}
                    />
                </Box>
                <Box width={[ 1, 1/2 ]} pt={4} ml={-5}>
                    <p css={css`
                        font-size: ${rhythm(0.8)};
                        color: #707070;
                    `}>
                        {data.site.siteMetadata.aboutLine1}
                        <br/>
                        <br/>
                        {data.site.siteMetadata.aboutLine2}
                        <br/>
                        <br/>
                        {data.site.siteMetadata.aboutLine3}
                    </p>
                </Box>
            </Flex>
        </Layout>
    )
}

export const query = graphql`
  query {
    site {
      siteMetadata {
        title
        profilPicture               
        aboutLine1
        aboutLine2
        aboutLine3
      }
    }
  }
`